body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --dark: #222;
  --light: #fafafa;
  --uc--light: #fafafa;
  --uc--dark: #222;
  --grey: #808083;
  --b2c: #c10230;
  --b2b: #558b6e;
  --ppl: #ff6600;
  --edu: #0033cc;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.dark {
  --dark: #fafafa;
  --light: #222;
}

.light {
  --dark: #222;
  --light: #fafafa;
}

.dark {
  background-color: #fafafa;
  background-color: var(--light);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  color: #222;
  color: var(--dark);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.container {
  width: 1280px;
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
  padding: 0 30px;
}

.dark *,
.light * {
  transition: 0.3s;
}

.dark-mode-button {
  position: fixed;
  top: 30%;
  right: 0;
  border: none;
  background-color: #222;
  background-color: var(--dark);
  color: #fafafa;
  color: var(--light);
  border: 1px solid #fafafa;
  border: 1px solid var(--light);
  padding: 10px 20px;
  -webkit-transform: rotate(90deg) translateY(15px);
          transform: rotate(90deg) translateY(15px);
  -webkit-transform-origin: right;
          transform-origin: right;
  z-index: 999999;
}

@media only screen and (max-width: 767px) {
  .App {
    overflow-x: hidden;
  }
  .dark-mode-button {
    top: 80%;
  }
}

.header {
  box-shadow: 0 0 10px rgb(207, 207, 207);
  position: -webkit-sticky;
  position: sticky;
  background-color: var(--light);
  color: var(--dark);
  top: 0;
  z-index: 999;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 50px;
  gap: 50px;
  padding: 10px 30px;
  width: 1280px;
  max-width: 100%;
  margin: 0 auto;
  overflow-x: hidden;
}

.dark .header {
  box-shadow: 0 0 10px rgb(0, 0, 0);
}

.logo {
  width: 240px;
}

.dark .st0 {
  fill: var(--dark);
}

.st1 {
  fill: #808083;
}

.nav {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
}

.nav a {
  text-decoration: none;
  font-size: 1.1rem;
  color: var(--dark);
}

.menu-icon {
  position: absolute;
  right: 40px;
  top: 20px;
  display: none;
}

.menu-icon svg {
  font-size: 30px;
  transition: 0.3s;
  position: absolute;
  background-color: var(--light);
}

#XMLID_27_,
#XMLID_24_ {
  -webkit-animation: springBoard 4s;
          animation: springBoard 4s;
}

@-webkit-keyframes springBoard {
  0% {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  50% {
    -webkit-transform: scaleX(6.5);
            transform: scaleX(6.5);
  }
}

@keyframes springBoard {
  0% {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  50% {
    -webkit-transform: scaleX(6.5);
            transform: scaleX(6.5);
  }
}

@media only screen and (max-width: 767px) {
  .header {
    max-width: 100%;
  }
  .logo {
    width: 180px;
  }
  .menu-icon {
    display: block;
  }
  .nav {
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    position: absolute;
    top: 0;
    right: 0;
    background-color: white;
    height: 100vh;
    width: 50%;
    padding: 20px;
    margin-top: 66px;
    transition: 0.3s;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  .dark .nav {
    background-color: var(--light);
    color: var(--dark);
  }
  .active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    overflow-x: visible;
  }
  .nav a {
    border-bottom: 1px solid rgb(206, 206, 206);
    padding-bottom: 20px;
  }
}

.footer {
  background: var(--uc--dark);
  color: var(--uc--light);
  padding: 4rem 0;
}

.footer * {
  color: var(--uc--light);
}

.dark .footer {
  border-top: 1px solid var(--grey);
}

.footerGrid {
  display: grid;
  grid-template-columns: 3fr 2fr 4fr;
  grid-gap: 50px;
}

.footerGridItem3 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
}

.footerGridItem3 img {
  width: 260px;
}

.footerGridItem3 img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.footerGridItem1 img {
  padding-right: 6px;
}

@media only screen and (max-width: 767px) {
  .footerGrid {
    grid-template-columns: 1fr;
  }
}

.full-width {
  width: 100%;
}

.banner {
  padding: 10vh 30px;
}

